body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  font-size:30px;
  text-transform: capitalize;
}

.min-max-card {
  width: 400px;
  margin: 20px auto;
  height: 90px;
  background: #fff;
  border-bottom: 1px solid #ccc;
}

.min-max-card-title {
  height: 40px;
  line-height: 40px;
  background: #e8e8e8;
  text-align: center;
  color: #606066;
  font-weight: bold;
  font-size: 19px;
}

.min-max-value {
  height: 30px;
  padding: 10px 0px;
  line-height: 30px;
  text-align: center;
  color: #000;
  font-weight: bold;
}

body,
h1,
div,
span {
  padding: 0;
  margin: 0;
}

.header {
  height: 50px;
  text-align: center;
  width: 100%;
  background: #606060;
  color: #fff;
  line-height: 50px;
}

.wrap {
  display: -webkit-flex;
  display: flex;
  width: 900px;
  margin: 20px auto;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  height: 700px;
}

.display-left {
  height: 700px;
  background: #FFF;
  width: 400px;
  border: 1px solid hsl(0, 0%, 38%);
  border-radius: 5px;
}

.input-form {
  margin-top: 20px;
  padding-bottom: 10px;
  width: 100%;
  border-bottom: 1px solid #fff;
  text-align: center;
  margin-bottom: 20px;
}

.sort-type {
  width: 380px;
  margin: 20px 10px;
}

.generated-numbers {
  height: 700px;
  background: #FFF;
  width: 400px;
  border: 1px solid #606060;
  border-radius: 5px;
}

.listItem {
  height: 20px;
  width: 100%;
  padding: 3px 0px;
  text-align: center;
  font-size: 12px;
  border-bottom: 1px solid #fff;
  line-height: 20px;
}

.title-text {
  width: 100%;
  height: 30px;
  line-height: 30px;
  background: #606060;
  text-align: center;
  color: white;
  font-weight: bold;
  font-size: 19px;
}

.download-button {
  margin-top: 20px;
  padding-bottom: 10px;
  width: 100%;
  text-align: center;
}

input[type=number] {
  width: 360px;
  padding: 5px 10px;
  margin-bottom: 10px;
  font-size: 16px;
  border-radius: 5px;
  background-color: #e8e8e8;
  border: none;
}

select {
  width: 100%;
  padding: 16px 20px;
  height: 30px;
  border: none;
  border-radius: 5px;
  background-color: #e8e8e8;
  font-size: 16px;
}

.clear-button, input[type=submit], .download-btn {
  background-color: #606060;;
  border-radius: 5px;
  color: white;
  padding: 5px 10px;
  text-decoration: none;
  margin: 4px 2px;
  cursor: pointer;
}

.display-numbers {
  max-height: 520px;
  overflow-y: scroll;
  text-align: center;
}

::-webkit-scrollbar {
  -webkit-appearance: none;
  width: 7px;
}
::-webkit-scrollbar-thumb {
  border-radius: 4px;
  background-color: rgba(0,0,0,.5);
  -webkit-box-shadow: 0 0 1px rgba(255,255,255,.5);
}

